.transaction_history {
  width: 330px;
  border-collapse: collapse;
  border: solid 1px rgb(211, 221, 221);
  text-align: center;
  margin: auto;
}
th {
  height: 60px;
  height: 50px;
  font-size: 15px;
  color: white;
  border: 1px solid grey;
  background-color: #00bcd4;
  padding: 5px 0 5px 0;
}

td {
  font-size: 13px;
  font-weight: 300;
  padding: 5px 0 5px 0;
  border: 1px solid grey;
}
